/* Scss Document */
/* work CSS */
body {
	#conts {
		.container {
			.conts_groop {
				padding-bottom: 0;
			}
			#work_list {
				padding-top: 0;	
				.page-sort-body-wrap {
					padding-left: 15px;
					padding-bottom: 3rem;
					.page-sort-body {
						padding-right: 3em;
						.t {
							padding-right: 1em;
							letter-spacing: 0.2em;
							display: inline-block;
							vertical-align: top;
						}
						.page-sort-select {
							width: 135px;
							height: 30px;
							color: rgba(0, 0, 0, 0.4);
							border-bottom: 1px solid rgba(0, 0, 0, 0.4);
							cursor: pointer;
							-webkit-transition: all 0.2s ease-in-out;
							-o-transition: all 0.2s ease-in-out;
							transition: all 0.2s ease-in-out;
							.in {
								position: relative;
								width: 100%;
								height: 100%;
								select {
									-webkit-appearance: none;
									-moz-appearance: none;
									appearance: none;
									border: none;
									background-color: transparent;
									border-radius: 0;
									height: 30px;
									width: 100%;
									padding: 0 20px 0 5px;
									display: block;
									color: rgba(0, 0, 0, 0.4);
									cursor: pointer;
								}
							}
							.page-sort-select-arrow {
								width: 20px;
								height: 30px;
								position: absolute;
								right: 0;
								top: 2px;
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;				
							}
						}
						.page-sort-select:hover select {
							color: #000000;
							border-color: #000000;
						}
					}
				}
				.page-sort-body, .page-sort-body-wrap {
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
				}
				
				.card {
					width: 18rem;
					display: block;
					border: none;
					&:hover, &:active, &:focus {
						text-decoration: none;
					}
					.imgWrap {
						overflow: hidden;
						position: relative;
  						width: 100%;
					}
					.imgWrap:before{
					  content: "";
					  display: block;
					  padding-top: 100%;
					}
					img {
						  position: absolute;
						  width: 100%;
						  height: 100%;
						  top: 0;
						  right: 0;
						  bottom: 0;
						  left: 0;
						  margin: auto;
						  object-fit: cover;
						
						&:hover, &:active, &:focus{
						transform: scale(1.1);
						transition-duration: 0.3s;
						opacity: 0.6;
						}
					}
				}
				.card-body {
					padding-left: 0;
					padding-top: 0.5rem;
					padding-bottom: 3rem;
					h5 {
						color: #212529;
					}
				}
			} /* End of #work_list */
		}
	}/* End of #conts */	
}
/*  CSS Media Query: On screens that are 1199px or less */
@media (max-width: 1199px) {
	#conts {
		h2 {
			font-size:  2rem !important;
		}
		p {
			font-size:  0.9rem !important;
		}
		#work_list {	
			.card {
				width: 100% !important;
				h5 {
					font-size:  0.9rem;
				}
			}
		}
	}
}
